import React, {useState,useEffect} from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import firebase from './firebase';
import './style.css';


const Data = () => {

    const [name,setName] = useState()
    const [surname,setSurname] = useState()
    const [location,setLocation] = useState()
    const [age,setAge] =useState()
    const [users,setUsers] = useState([])
    const db = firebase.firestore();

    const getname = (e) => {
        setName(e.target.value)
    }

    const getsurname = (e) => {
        setSurname(e.target.value)
    }

    const getlocation = (e) => {
        setLocation(e.target.value)
    }

    const getage = (e) => {
        setAge(e.target.value)
    }



    const createUser = (e) =>{
        e.preventDefault();
        db.collection('users').add({
            name:name,
            surname:surname,
            location:location,
            age:age
        })
        .then((res) => {console.log('user created')})
        .catch((err) => {console.log(err)})
    }

    //get all users

    useEffect(() => {
        let userinfo =[];
        db.collection('users').get()
        .then((res) => {
            res.forEach(action => {
                userinfo.push({...action.data(), id: action.id});
                console.log(action.data())
            })
            setUsers(userinfo);

        })
    },[])

    //delete function
    const deleteUser = (e) => {
        let uid = e.target.id
        db.collection('users').doc(uid).delete()
        .then(() => {console.log('user deleted')})
        .catch((err) => {console.log(err)})
    }
    


    //update function
    const updateUser = (e) => {
        let uid = e.target.id
        db.collection('users').doc(uid).update({
            name : name,
            surname: surname,
            location:location,
            age:age
        })
        .then(() => {console.log('user updated')})
        .catch((err) => {console.log(err)})
    }
       

    

    return (
        <>
        <div className ={"container mt-5"}>
            <form onSubmit = {createUser}>

            <div className="input-group">
            <span className="input-group-text">Name and Surname</span>
            <input name = "name" onChange={getname} type="text" aria-label="First name" class="form-control" placeholder="Enter your name"></input>
            <input name ="surname" onChange={getsurname} type="text" aria-label="Last name" class="form-control" placeholder =" Enter your Surname"></input>

            <div className="input-group">
            <span className="input-group-text">Location and Age</span>
            <input name = "location" onChange={getlocation} type="text" aria-label="Location" class="form-control" placeholder="Enter Location"></input>
            <input name ="age" onChange={getage} type="text" aria-label="Age" class="form-control" placeholder = "Enter Age" ></input>
            </div>
            
            <button type="submit" className="btn btn-secondary">Add</button>
            </div>

            </form>

            {/*display users from firestore*/}
               {
                   users.map((action) => ( 
                    <div className="card mt-4" key ={ action.id }>
                        
                    <div className="card-header">
                        Custom user
                   </div>
                   <div className="card-body">
                   <h5 className="card-title">User Details</h5>
                   <p className="card-text">{ action.name}  {action.surname}  {action.location}  {action.age}</p>
                   <button id ={ action.id } onClick = { deleteUser} className="btn btn-danger me-2">Delete user</button>
                   <button id ={ action.id } onClick = { updateUser} className="btn btn-secondary">Update user</button>
                 </div>

                   </div>
        

                   ))
               }

            
        </div>

        </>
    )
} 
export default Data